import {Component, createMemo, createSignal, onCleanup, useContext} from "solid-js";
import {appState} from "../../../../config/app.state";
import {LoadableReady, WalletsModalCloseReason} from "../../../../models";
import {ConnectAdditionalRequest, EngineConnectInfo} from "@okxconnect/tonsdk";
import {SingleWalletModal} from "../../../../app/views/modals/wallets-modal/single-wallet-modal";
import {ConnectorContext} from "../../../../config/connector.context";
import {OKXConnectUiContext} from "../../../../config/okx-connect-ui.context";
import {isMobile} from "../../../../app/hooks/isMobile";
import {showTgWallet} from "../../../../app/utils/wallets";

export const TonSingleWalletModal: Component = () => {
  const tonConnectUI = useContext(OKXConnectUiContext)!;
  const connector = useContext(ConnectorContext)!;
  const additionalRequestLoading = (): boolean =>
      appState.connectRequestParameters?.state === "loading";

  const [universalLinkParams, setUniversalLinkParams] = createSignal<EngineConnectInfo | null>(null);

  const isSingleWallet = (): boolean => !showTgWallet();
  const openUniversalLink = (): boolean => {
    if (isSingleWallet()){
      return isMobile()
    }
    return false
  };

  const additionalRequest = createMemo(() => {
    if (additionalRequestLoading()) {
      return undefined;
    }
    return (
      appState.connectRequestParameters as LoadableReady<ConnectAdditionalRequest>
    )?.value;
  });

  const onClose = (closeReason: WalletsModalCloseReason): void => {
    tonConnectUI.closeModal(closeReason);
  };

  const unsubscribe = connector.onStatusChange((wallet) => {
    if (wallet) {
      onClose("wallet-selected");
    }
  });
  onCleanup(unsubscribe);

  return (
    <SingleWalletModal
      loadingBeforeConnect={() => {
        return appState.connectRequestParameters?.state === "loading";
      }}
      connect={() => {
        return new Promise<string>((resolve, reject) => {
          setUniversalLinkParams(null)
          connector.connect({
            openUniversalLink: openUniversalLink(), // must be same as isSingleWallet
            tonProof: additionalRequest()?.tonProof,
            redirect: additionalRequest()?.redirect,
          }).then((url)=>{
            resolve(url);
          }).catch((error)=>{
            reject(error);
            setUniversalLinkParams(null)
          });
        })
      }}
      onClose={onClose}
      universalLinkParams={universalLinkParams}
      isSingleWallet={isSingleWallet()} // must be same as openUniversalLink
    />
  );
};
